.field{
    display: flex;
    flex-direction: row;
    align-content: center;
    margin-bottom: 5px;
    margin-top: 20px;
    justify-content: space-between;
    label{
        color: #AD8350;
        font-weight: 700;
        font-size: 1rem;
        display: flex;
        align-items: center;
    }
    input{
        width: 240px;
        padding: 10px;
        border-style: none;
        background-color: #AFA060;
    }

}