.secondary-nav{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    background-color: #764134;
    .flat-btn{
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: rgba(255, 255, 255, 0);
        color: #000000;
        border: none;
        height: 40px;
        font-family: Montserrat;
        font-size: 1rem;
    }
}


.field{
    display: flex;
    flex-direction: row;
    align-content: center;
    margin-bottom: 5px;
    justify-content: space-between;
    label{
        color: #AD8350;
        font-weight: 700;
        font-size: 1rem;
        display: flex;
        align-items: center;
    }
    input{
        width: 240px;
        padding: 10px;
        border-style: none;
        background-color: #AFA060;
    }

}

.contact-edit{
    display: flex;
    flex-direction: column;
    img{
        width: 70px;
        height: auto;
    }
    .form-btn{
        border-style: none;
        background-color: #764134;
        padding: 10px;
        cursor: pointer;
        min-width: 70px;
        float: right;
    }
}