@import '../../../../assets/fonts/Monteserrat/Montserrat-Regular.ttf';

.filter-container{
    height: 40px;
    background-color: #764134;
    padding: 5px;
    display: flex;
    justify-content: center;
    input{
        width: 90%;
        padding: 4px 0;
        border-style: none;
        outline: none;
        background-color:rgba(255, 255, 255, 0);
        color: #000000;
        font-family: Montserrat;
        font-size: 1rem;
    }
    ::placeholder{
        color: #0000009a;
    }
}