.contact-details-container{

    .transfer-section{
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        width: 300px;
        margin-bottom: 20px;

        input{
            padding: 10px;
            background-color: #AFA060;
            border-style: none;
            outline: none;
            color: #000000;
            font-family: Montserrat;
            font-size: 1rem;

        }
        ::placeholder{
            color: #0000009a;
        }
        .form-btn{
            border-style: none;
            background-color: #764134;
            padding: 10px;
            cursor: pointer;
            min-width: 70px;
        }
    }
}

.flat-btn{
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(255, 255, 255, 0);
    color: #000000;
    border: none;
    height: 40px;
    font-family: Montserrat;

}

.secondary-nav{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    background-color: #764134;
    padding: 0 20px;
}

.contact-details{
    display: flex;
    flex-direction: column;
    margin-bottom: 20px;
    img{
        width: 70px;
        height: auto;
    }
    :first-child{
        color: white;
    }
    span{
        color: #AFA060;
        margin-bottom: 10px;
    }
}