*{
    box-sizing: border-box;
}

body{
    width: 100vw;
    height: 90vh;
    font-family: Montserrat;
    overflow-x: hidden;
    background-color: #2A1A1F;
    
}

a{
    text-emphasis: none;
    text-decoration: none;
    cursor: pointer;
}

h1,h2,h3,h4,h5,h6{
    display: block;
    font-size: 0.83em;
    margin: 0;
    font-weight: normal;
}

.home-container{
    background-color: #764134;
    padding: 10px 0;
    margin-bottom: 10px;

}

.container{
    margin: 0 20px;
}
.disconnected-message{
    margin: 20px 0;
    color: #AD8350;

}