.header-container{
    background-color: #AFA060;
    width: 100%;
    .h-container{
        .menu-btn{
            display: inline-block;
            padding: 10px;
            font-size: 1.5rem;
        }
        h4{
            color: #000000;
    
        }
        a{
            width: 150px;
            line-height: 40px;
            // margin-right: 10px;
            padding: 10px;
            border-bottom: 1px solid black;
            color: #000000;
            cursor: pointer;
        }

    }
}
@media (max-width: 830px) {

    .main-nav{
        display: flex;
        flex-direction: column;
        height: 100vh;
        position: fixed;
        top: 0;
        right: 0;
        background: rgba(255, 255, 255, 0.9);
        border-left: 1px solid rgba(153, 153, 153, 0.5);
        transform: translate(100%, 0);
        transition: transform .6s;
        svg{
            margin-right: 25px;
        }
        .exit-btn{
            width: 100%;
            line-height: 40px;
            padding: 10px;
            text-align: center;
            border-bottom: 1px solid black;
        }
    }

    .open-menu.main-nav{
        transform: translate(0, 0);
    }
}
